import React from "react";

// component
import { Container, Row, Col, Image } from "react-bootstrap";
import { isMobile } from "react-device-detect";
import { importAll } from "../components/Functions/PageFunctions";
import Swal from "sweetalert2";
import axios from "axios";

export default function PageGive() {
  const giveImages = importAll(
    require.context("../images/give/", false, /\.jpg|\.png|\.jpeg/)
  );

  const donationClick = async () => {
    const { value: formValues } = await Swal.fire({
      title: "Enter amount to give.",
      input: "number",
      inputLabel: "Minimum amount: 100",
      focusConfirm: false,
      inputValidator: (value) => {
        if (!value || value < 100) {
          return "Please input a minimum amount of 100.";
        }
      },
      showClass: {
        backdrop: "swal2-noanimation", // disable backdrop animation
        popup: "", // disable popup animation
        icon: "", // disable icon animation
      },
      hideClass: {
        popup: "", // disable popup fade-out animation
      },
    });

    if (formValues) {
      const getUrl = process.env.REACT_APP_API_URL + "/api/create_payment_link";

      await axios
        .post(getUrl, {
          payment_amount: formValues,
          description: "payment-wtbs",
          remarks: "TEST",
        })
        .then((result) => {
          window.open(result.data.checkout_url);
        });
    }
  };

  return (
    <>
      <Container fluid className="m-0 p-0 w-100">
        <Row
          lg={12}
          className={`page-divider m-0 p-0 ${isMobile ? "w-100" : "w-75"} h-25`}
        >
          <Col lg={12} className="h-100 align-items-center d-flex">
            <h1
              className={`page-divider-text p-5 ${
                isMobile ? "text-center" : ""
              }`}
            >
              Give Now
            </h1>
          </Col>
        </Row>
      </Container>
      <Container fluid className="m-0 p-0">
        <Row className="m-0 p-0 mt-5">
          <Col xs={12} lg={12} className="p-5 text-center">
            <p>
              Giving is a privilege of the royal priesthood of every believer
              and should be motivated by the believer’s gratitude as a result of
              his own spiritual growth. “Let each one give just as he has
              purposed in his heart; not grudgingly or under compulsion; for God
              loves a cheerful giver” ( 2 Cor. 9:7 ). However, a few words of
              caution are in order. First, please do not give if your desire is
              to be blessed through financial giving. As New Testament Church
              Age believers, being blessed through monetary giving is not a
              genuine motive that God honors. Rather, Christians should give
              because of our awareness that we have already been blessed with
              every spiritual blessing. Ephesians 1:3 says, “Blessed be the God
              and Father of our Lord Jesus Christ, who has blessed us with every
              spiritual blessing in the heavenly places in Christ.” Giving
              should be done with a worshipful and thankful heart that overflows
              into an attitude of gratitude on account of our already existent
              spiritual blessings. Furthermore, please do not give to this
              ministry if you are not a believer. First, place your trust in
              Christ alone for the safekeeping of your soul and then begin to
              grow in knowledge of His Word. The Holy Spirit will later convict
              of your privilege to give to support God’s work. It also should be
              understood that you cannot earn salvation through financial
              giving. Faith alone in Christ alone saves and giving is the
              privilege of the child of God who has already been born again by
              the Spirit of God. Finally, if you are not a member or regular
              attender of Sugar Land Bible Church and are still blessed by this
              ministry resulting in a desire to give to it, we would ask that
              you give first to support your own home church. After giving to
              your own local church, or if you do not have a church home of your
              own, we welcome your financial giving to Sugar Land Bible Church.
            </p>
          </Col>
          <Col xs={12} lg={12} className="text-center">
            <Image
              src={giveImages[0]}
              className="py-5 pill-cursor"
              onClick={() => donationClick()}
            />
          </Col>
        </Row>
      </Container>
      <Container fluid className="m-0 p-0 h-15"></Container>
    </>
  );
}
